  font-family: "Tahoma"; /* Choose a name for your font */
  src: url("../../../../../assets//fonts//Tahoma\ Regular\ font.ttf")
      format("woff2"),
    /* Modern Browsers */
      url("../../../../../assets//fonts/Tahoma\ Regular\ font.ttf")
