.LatestEventCard {
  width: 300px;
  height: 400px;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  box-shadow: 0px 0px 10px 0px rgba(205, 43, 48, 1);
}

.LatestEventCardNewCardDesign {
  width: 300px;
  height: 420px;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  transition: transform 0.1s linear;
}

.LatestEventCardNewCardDesign:hover {
  transition: transform 0.1s linear;
  transform: scale(0.96);
}
/* --- card_uper_vector1 */
.card_uper_vector1 {
  /* border-radius: 8px 8px 50px 8px ; */
  clip-path: polygon(115% 0, 0 0, 0 115%);
  border-radius: 4px 4px 0px 4px;
}
.db_image {
  /* clip-path: polygon(19% 0, 100% 0, 100% 68%, 71% 100%, 0 100%, 0% 60%, 0 13%); */
  /* clip-path: polygon(19% 0, calc(100% - 7px) 0, 100% 68%, 100% calc(100% - 7px), 57% 100%, 7px 100%, 0% 60%, 0 13%); */
  /* clip-path: polygon(19% 0%, calc(100% - 9px) 0%, 100% 68%, 100% calc(100% - 121px), 57% 100%, 35px 100%, 0% 60%, 0 13%); */
  padding: 0px 10px;
  height: 100%;
  width: 100%;
  z-index: 20 !important;
  position: relative;
}
.clip_path {
  width: 250px;
  height: 332px;
  /* clip-path: polygon(70.71% 100%, 100% 70.71%, 100% 29.29%, 100% 0%, 29.29% 0%, 0% 29.29%, 0% 70.71%, 0% 100%); */
  clip-path: polygon(19% 0, 100% 0, 100% 68%, 71% 100%, 0 100%, 0% 60%, 0 13%);
  background-color: #eb8b47;
  overflow: hidden;
  margin: 0px auto;
  z-index: 50 !important;
  position: relative;
}

.card_bottom_vector1 {
  border-radius: 16px 8px 8px 8px;
  /* clip-path: polygon(105% 0, 0 0, 0 105%); */
  clip-path: polygon(100% 0, 100% 100%, 0 100%);
}
.imageoverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background: linear-gradient(180.07deg, rgba(0, 0, 0, 0) 17.96%, #8A0000 99.94%);
  padding: 0px 10px; */
}
.new_card_footer {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}

.new_card_btn {
  margin-top: 4px;
  border-radius: 5px;
  padding: 8px 10px;
  background-color: #c10f0f;
  font-family: Montserrat;
  font-size: 13px;
  font-weight: 900;
  letter-spacing: 0em;
  text-align: left;
}

.date_of_new_card {
  font-family: Krick !important;
  font-size: 35px;
  font-weight: 700;
  line-height: 80px;
  letter-spacing: 0em;
  text-align: left;
  margin-top: -20px;
}

.month_of_new_card {
  /* font-family: Arial !important; */
  font-family: Krick !important;
  font-size: 17px;
  font-weight: 700;
  line-height: 60px;
  letter-spacing: 0em;
  text-align: left;
  margin-top: -10px;
}

.time_text_of_new_card {
  font-family: "Montserrat", sans-serif !important;
  font-size: 14px;
  font-weight: 800;
  line-height: -36px;
  letter-spacing: -0.03em;
  text-align: left;
}

.time_of_new_card {
  background-color: white;
  color: black;
  padding: 1px 3px 1px 3px;
  border-radius: 2px;
  font-family: "Montserrat", sans-serif !important;
  font-size: 10px;
  text-align: center;
