}
.Name_of_the_event {
  font-family: Arial !important;
  font-size: 32px;
  font-weight: 700;
  text-align: left;
  line-height: -60;
}
/* ============= */

.LatestEventCard img {
  width: 100%;
  height: 100%;
  object-fit: contain;

  transform: scaleX(1);
  transform-origin: left;
  transition: transform 0.2s linear;
}

.LatestEventCard .overlaynew {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(30, 30, 30, 0.32);
  width: 300px;
  height: 400px;
  z-index: 1;
  display: none;
}

/* ---- LatestEventCard_top_right_section */
.LatestEventCard_top_right_section {
  position: absolute;
  z-index: 2;
  top: 20px;
  right: 20px;
  display: flex;
  gap: 10px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
}

.LatestEventCard_top_right_section_box {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px;
  border-radius: 9px;
  gap: 7px;
  background: linear-gradient(
      0deg,
      rgba(130, 19, 23, 0.4),
      rgba(130, 19, 23, 0.4)
    ),
    linear-gradient(
      94.21deg,
      rgba(205, 41, 46, 0.8) 1.47%,
      rgba(205, 41, 46, 0.4) 98.9%
    );
  width: fit-content;
}

.LatestEventCard_top_right_section_box p {
  font-family: Tahoma;
  font-size: 11px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0em;
  text-align: left;
}

.LatestEventCard_footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 72px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Arial;
  font-size: 17px;
  font-weight: 600;
  line-height: 20px;
  z-index: 2;
  background: rgba(205, 43, 48, 1);
}

/* ===========  */
.ticket_box_content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  display: none;
}

.LatestEventCard:hover .LatestEventCard_top_right_section {
  display: none;
}

.LatestEventCard:hover .LatestEventCard_footer {
  display: none;
}

.LatestEventCard:hover .ticket_box_content {
  display: block;
}

.LatestEventCard:hover .overlaynew {
  display: block !important;
}

.LatestEventCard:hover img {
  transform: scaleX(1.3);
  transform-origin: left;
  transition: transform 0.2s linear;
}

/* ============  */
.inner_ticket_location {
  background: white !important;
