.Trending_heading {
  /* font-family: Cabinet Grotesk;
font-size: 38px;
font-weight: 700;
line-height: 51px;
letter-spacing: 0em;
text-align: left; */
  margin-bottom: 40px;
  color: #cd2319;
  letter-spacing: 5px;
}
/* ---- trending_blog_grid_layout */
.trending_blog_grid_layout {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 30px;
}
/* ---- left_grid_layout */
.left_grid_layout {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.left_grid_layout h2 {
  font-family: Cabinet Grotesk;
  font-size: 22px;
  font-weight: 500;
  text-align: left;
  /* max-width: 500px; */
}
.left_grid_layout p {
  font-family: Cabinet Grotesk;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: 0em;
  text-align: left;
  color: gray;
  margin: 10px 0px;
  max-width: 850px;
}
.left_grid_layout img {
  width: 100%;
  height: 500px;
  object-fit: cover;
  border: 1px solid #a60d0d;
  border-radius: 15px !important;
}
.left_grid_layout button {
  text-align: left;
  padding: 7px 12px 7px 12px;
  background: #a60d0d;
  font-family: Cabinet Grotesk;
  font-size: 18px;
  font-weight: 700;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: center;
  max-width: 120px;
  border-radius: 7px;
  margin: 0px 10px;
}

/* -------------- right_grid_layout_box */
.right_grid_layout_box {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 20px;
}
.right_grid_layout_box img {
  width: 200px !important;
  height: 200px !important;
  border-radius: 15px !important;
  object-fit: cover;
  /* border: 1px solid #A60D0D; */
}
.right_grid_layout_box_content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 100%;
  max-width: 100%;
  gap: 20px;
  padding: 10px 0px;
}
.right_grid_layout_box_content h2 {
  font-family: Cabinet Grotesk;
  font-size: 17px;
  font-weight: 700;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: left;
}
.right_grid_layout_box_content p {
  font-family: Cabinet Grotesk;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: 0em;
  text-align: left;
  color: gray;
}
.right_grid_layout_box_content button {
  color: #a60d0d;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 4px;
  cursor: pointer;
}

/* --- media querrys  */
@media (max-width: 820px) {
  .trending_blog_grid_layout {
    flex-direction: column;
    gap: 40px;
  }
  .left_grid_layout h2 {
    max-width: 100% !important;
  }
  .left_grid_layout img {
    width: 100%;
  }
  .right_grid_layout_box_content {
    max-width: 100%;
  }
  .right_grid_layout {
    display: flex;
    flex-direction: row !important;
    flex-wrap: nowrap;
    max-width: 1500px;
    overflow-x: auto !important;
    gap: 20px;
    padding: 10px 0px;
  }
  .right_grid_layout_box {
    flex-direction: column;
    max-width: 330px;
    border: 1px solid rgb(104, 104, 104);
    padding: 10px;
    border-radius: 8px;
    flex-shrink: 0;
  }
  .right_grid_layout_box img {
    width: 100% !important;
  }
}
@media (max-width: 450px) {
  .right_grid_layout_box {
    margin: 10px 0px;
    gap: 10px;
  }

  /* .right_grid_layout_box img {
    width: 150px !important;
    height: 150px !important;
  } */
  .right_grid_layout_box_content {
    padding: 0px;
    gap: 5px;
  }
  .right_grid_layout_box_content h2 {
    font-family: Cabinet Grotesk;
    font-size: 15px;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 0em;
    text-align: left;
  }
  .right_grid_layout_box_content p {
    font-family: Cabinet Grotesk;
    font-size: 12px;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0em;
    text-align: left;
    color: gray;
  }
  .right_grid_layout_box_content button {
    color: #a60d0d;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 4px;
    cursor: pointer;
  }
}
